* {
  box-sizing: border-box;
}

.header h1 {
  height: 100%;
  margin-top: 0;
  margin-bottom: 0;
  padding-bottom: 1vw;
  font-size: 12vw;
}

.header {
  float: left;
  width: 100%;
  height: 35%;
  text-align: center;
  color: #10686827;
  font-family: 'IBM Plex Mono', monospace;
  overflow: hidden;
  transition: background-image 2s;
  padding: 0 0;
}

.header:hover {
  background-image: url('aqua.gif');
  color: whitesmoke;
  background-size: cover;
}

.menu {
  float: left;
  width: 40%;
  height: 65%;
  text-align: center;
  overflow: hidden;
}

.menu a {
  background-color: white;
  padding-top: 0;
  display: block;
  width: 100%;
  height: 25%;
  color: #c7c7c7; 
  font-family: 'Cute Font', cursive;
  text-decoration: none;
  transition: background-color 0.5s, color 0.7s;
  font-size: 13vh;
}

.menu a:hover {
  background-color: hsla(330, 100%, 58%, 0.568);
  color: white;
}

.main {
  float: left;
  width: 60%;
  height: 65%;
  padding: 3.3% 3% 0 2%;
}

.main p {
  margin-top: 0;
  margin-bottom: 2.5vh;
  font-family: 'Montserrat', sans-serif;
  font-weight: bold;
  font-size: 145%;
  text-indent: 45px;
}

.main:hover {
  background-image: url('manteca_cat_ghost.jpg');
  background-size: cover;
  color: hsla(330, 100%, 58%, 1);
}

.main p:hover {
  background-color: inherit;
}

@media only screen and (max-width: 620px) {
  /* For Mobile: */
  .menu, .main .header {
    overflow: hidden;
    width: 100%;
  }

  .header {
    background-image: url('aqua.gif');
    color: whitesmoke;
    background-size: cover;
    margin-bottom: -10;
    margin-top: -10;
  }

  .header h1 {
    display: flex;
    justify-content: center;
    align-items: center;
    padding-left: 10%;
    padding-right: 10%;
    color: #ec039f;
    font-size: 14vw;
  }

  #nav1 {
    display: flex;
    justify-content: center;
    align-items: center;
    color: hsla(0,0%,100%,.7);
    background-color: #24292e;
  }

  #nav2 {
    display: flex;
    justify-content: center;
    align-items: center;
    color: rgb(255, 255, 255);
    background-color: #0073b1;
  }

  #nav3 {
    font-size: 17vw;
    display: flex;
    justify-content: center;
    align-items: center;
    /* border-top: 8px dotted rgb(18, 255, 243); */
    
  }

  #nav4 {
    font-size: 15vw;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    background-color: hsla(330, 100%, 58%, 1);
  }

  .main {
    margin: 0;
    padding: 5% 4% 1% 5%;
    line-height: 1.4;
    width: 100%;
    /* display: inline-block; */
    cursor: pointer;
    background-image: url('aqua_gold.gif');
    background-size: auto 100%;
    background-repeat: no-repeat;
  }

  .main p {
    color: white;
    background-color: rgba(255, 153, 0, 0.411);
    font-size: 2.1vh;
    margin-top: 0;
    margin-bottom: 10px;
    text-indent: 45px;
  }
}